<template>
  <div></div>
</template>

<script>
  // TODO: ws
  import {mapActions, mapGetters} from 'vuex'
  import {Toast} from 'vant';


  export default {
    data() {
      return {
        ws: null
      }
    },
    created() {
      //


      this.doRun()
    },

    methods: {
      /**
       * websocket
       */
      doRun(){
        try {
          if ('WebSocket' in window) {
            const uri = this.GLOBAL_MSG.env.WS_URI;
            this.ws = new WebSocket(uri+"/websocket/1995");
            console.log("websocket");
          }
        } catch (e) {
          alert('new WebSocket');
          return;
        }
        this.ws.onopen = this.onOpen;
        this.ws.onmessage = this.onMessage;
        this.ws.onerror = this.onError;
        this.ws.onclose = this.onClose;
      },
      //
      onOpen(e) {
        // alert('connect success!');
      },

      //()
      onMessage(e) {
        let chatInfo = JSON.parse(e.data);
        this.getMessage({chatInfo}) // MARK: {},?{chatInfo: chatInfo}
      },

      //
      onError(e) {
      },
      onClose(e) {

      },
      //
      doSend() {
        if (this.ws.readyState == this.ws.OPEN) {
          let msg = "11";
          this.ws.send(msg);//handleTextMessage
          alert("!");
        } else {
          alert("!");
        }
      },
      //
      doClose() {
        this.ws.close();
      },
      /**
       * vuex
       */
      ...mapActions([
        'getMessage','connectWS','isOnline'
      ]),
      myAlert(){
        Toast.loading({
          mask: true,
          message: '...'
        });
      },
    },
    watch: {
      //vuex+watch
      showIsConnect(newValue, oldValue){
        if (newValue != oldValue && newValue) {
          // this.doRun();
        }
      }
    },
    computed: {

      /**
       * vuex
       */
      ...mapGetters([
        'showChat','showMyInfo','showIsConnect','showIsOnline'
      ])
    }
  }
</script>


<style scoped>

</style>



