import Vue from 'vue';
import vuex from 'vuex'


Vue.use(vuex);


export default new vuex.Store({
  modules: {

    //
    modChat:{

      //
      state: {
        chatArr:[],
        myChat:{}
      },
      mutations:{
        _getMessage(state, {chatInfo}){
          state.chatArr.push(chatInfo);
        },

      },
      actions: {
        //websocketonMessage(websocket)
        getMessage(context, {chatInfo}) {
          context.commit('_getMessage',{chatInfo})
        }
      },
      getters: {
        showChat(state) {
          return state.chatArr;
        },
        sendMyChat(state) {
          return state.myChat;
        }
      }
    },
    modUser:{
      state: {
        myInfo:{}
      },
      mutations:{
        //
        _setMyInfo(state,{userInfo}) {
          state.myInfo = userInfo;
        }
      },
      actions: {
        setMyInfo(context,{userInfo}){
          context.commit("_setMyInfo", {userInfo})
        }
      },
      getters: {
        showMyInfo(state){
          return state.myInfo;
        }
      }

    },
    modWebsocket:{
      state:{
        isConnect: 0,
        isOnline: false
      },
      mutations: {
        _connectWS(state){
          state.isConnect += 1;
          alert(state.isConnect)
        },
        _isOnline(state, {isOnline}){
          state.isOnline = isOnline;
        },

      },
      actions: {
        connectWS(context) {
          context.commit('_connectWS');
        },
        isOnline(context, {isOnline}) {
          context.commit('_isOnline', {isOnline});
        }
      },
      getters: {
        showIsConnect(state) {
          return state.isConnect;
        },
        showIsOnline(state) {
          return state.isOnline;
        }
      }
    }
  }



})



