<template>
  <div class="rank-container">
    <van-list
      class="rank-van-list"
      v-model="loading"
      :finished="finished"
      :offset="10"
      @load="onLoad"
    >
      <!--vantuivanlist-->
      <div v-for="(obj,index) in list" :key="index" class="rank-card van-hairline--bottom">
        <div class="rank-card-img">
          <img :src='obj.imgUrl' height="100%" width="100%">
        </div>
        <div class="rank-card-content">
          <div class="van-ellipsis" style="font-size: 18px">{{obj.title}}</div>
          <div class="van-ellipsis" style="color: #999;">{{obj.title}}</div>
          <div style="position: absolute;bottom: 0px;">
            <span style="color: #999;" class="fa fa-youtube-play"></span>
            &nbsp;
            <span style="color: #999;">{{numFormat(obj.play)}}</span>
            &nbsp;&nbsp;&nbsp;
            <span style="color: #999;" class="fa fa-drivers-license-o"></span>
            &nbsp;
            <span style="color: #999;">{{numFormat(obj.view)}}</span>
          </div>
        </div>
      </div>
    </van-list>
  </div>
</template>

<script>
  export default {
    data(){
      return {
        loading: false,
        finished: false,
        list: [
          /* {title: 'V101 MIKU',imgUrl: require('../../../assets/image/girl/girl1.jpg'),play: 165000, view: 840},
           {title: '',imgUrl: require('../../../assets/image/girl/girl2.jpg'),play: 372000, view: 958},
           {title: '4',imgUrl: require('../../../assets/image/girl/girl3.jpg'),play: 940000, view: 587}*/

        ]


      }
    },
    mounted() {

    },
    methods: {
      onLoad() {
        setTimeout(() => {

          this.list.push({title: '3',imgUrl: require('../../../assets/image/rank/rank1.jpg'),play: 165000, view: 840});
          this.list.push({title: 'C!: , 13',imgUrl: require('../../../assets/image/rank/rank2.jpg'),play: 372000, view: 958});
          this.list.push({title: '156 ',imgUrl: require('../../../assets/image/rank/rank3.jpg'),play: 940000, view: 587});
          this.list.push({title: '',imgUrl: require('../../../assets/image/rank/rank4.jpg'),play: 75100, view: 115});
          this.list.push({title: 'TOP10 VOL403 ',imgUrl: require('../../../assets/image/rank/rank5.jpg'),play: 15800, view: 840});
          /*    this.list.push({title: '',imgUrl: require('../../../assets/image/girl/girl6.jpg'),play: 200000, view: 961});
              this.list.push({title: '',imgUrl: require('../../../assets/image/girl/girl7.jpg'),play: 165000, view: 250});
              this.list.push({title: '',imgUrl: require('../../../assets/image/girl/girl8.jpg'),play: 750000, view: 134});
              this.list.push({title: '',imgUrl: require('../../../assets/image/girl/girl9.jpg'),play: 1685000, view: 668});
  */

          this.loading = false;
          if (this.list.length >= 50) {
            this.finished = true;
          }
        }, 500);
      },
      //
      numFormat(num){
        if (num/10000 >= 1) {
          num = num / 1000 +"";
        }
        return num;
      }


    }
  }
</script>

<style scoped>

  .rank-container{
    z-index: -1;
    position: fixed;
    width: 100%;
    top: 116px;
    bottom: 50px;
    left: 0;
    /*// TODO: border-bottom;border-bottom */
    border-bottom: 1px solid #ffffff;/* // MARK: */
    background-color: #f8f8f8;
    overflow-y: auto;
    /*box-sizing: border-box;*/

  }



  .rank-card{
    position: relative;
    height: 100px;
    width: 100%;
    margin: 5px 0;
    background-color: #ffffff;
  }


  .rank-card-img{
    position: absolute;
    width: 120px;
    height: 80px;
    margin: 10px;
  }

  .rank-card-content{
    position: absolute;
    left: 140px;
    right: 20px;
    height: 80px;
    margin: 10px;
  }


</style>



